<script>
  import {Menu} from 'svelteBricks'
  import DefaultComponent from './DefaultComponent.svelte'
  import Prism from './Prism.svelte'
  export let rows;
  export let width;


$: columns = rows.splice(0,1)[0]

</script>

<style>th {
  text-align: left;
  font-weight: bold;
}

td:first-child {
  font-weight: bold;
}

tr {
  margin-left: -4px;
  margin-right: -4px;
}

tr > * {
  padding-left: 4px;
  padding-right: 4px;
}

* :global(.menu) {
  background-color: #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  padding: 1rem
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<table>
  <thead>
    <tr>
      {#each columns as column}
        <th>{column}</th>
      {/each}
    </tr>
  </thead>
  <tbody>
    {#each rows as row}
      <tr>
        {#each row as value, i}
          <td style="{width[i]? `width: ${width[i]}`:''}">
          {#if typeof value === 'string'}
            {value}
          {:else}
            <svelte:component this={value.toggle? Menu: DefaultComponent }>            
                <button class="btn sm green outline" slot="activator">
                
                Show
                </button>
                <svelte:component this={value.format ? Prism: DefaultComponent} format={value.format}>
                {value.body}            
                </svelte:component>
            </svelte:component>
          {/if}
          </td>
        {/each}
      </tr>
    {/each}
  </tbody>
</table>

